<template>
  <div>
    <div class="center">
      <div class="box">
        <form @submit.prevent="login">
          <div class="form">
            <h2>Kirish</h2>
            <div class="inputBox">
              <span>Foydalanuvchi (Login)</span>
              <input v-model="username" type="text" >

            </div>
            <div class="inputBox">
              <span>Parol</span>
              <input type="password" v-model="password" >

            </div>
            <button class="login-btn">
              <span v-if="is_spinner">Kirish</span>
              <b-spinner v-if="!is_spinner" variant="#ffffff"></b-spinner>
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data(){
    return{
      username:'',
      password:'',
      is_spinner:true
    }
  },
  methods:{
    login(){
      this.is_spinner=false
      this.$axiosDefault.post('Account/GenerateToken',{
        "username":this.username,
        "password":this.password
      })
      .then(res=>{
        localStorage.setItem('token',res.data.token)
        this.$router.push('/')
      })
      .catch(()=>{
        this.is_spinner=true
        this.$toast("Foydalanuvchi nomi yoki parol xato!", {
          timeout: 2000,
          type:'error'
        });
      })
    }
  }
}
</script>
